import { Component, Prop, State, Event, EventEmitter, h, AttachInternals } from '@stencil/core';
import { syncCheckableFormValue } from '../../utils/form';

export type RadioButtonSize = 'sm' | 'md';

@Component({
  tag: 'bh-radio-button',
  styleUrl: 'bh-radio-button.css',
  shadow: true,
  formAssociated: true,
})
export class BhRadioButton {
  @AttachInternals() internals!: ElementInternals;
  /**
   * The size of the radio button
   */
  @Prop() size: RadioButtonSize = 'md';

  /**
   * Whether the radio button is selected
   */
  @Prop({ mutable: true }) selected: boolean = false;

  /**
   * Whether the radio button is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * The value associated with this radio button
   */
  @Prop() value: string = '';

  /**
   * The name of the radio group
   */
  @Prop() name: string = '';

  /**
   * Optional label text - when provided, renders radio with label
   */
  @Prop() label: string = '';

  /**
   * Optional supporting text below the label
   */
  @Prop() supportingText: string = '';

  /**
   * Track focus state internally
   */
  @State() isFocused: boolean = false;

  /**
   * Track hover state internally
   */
  @State() isHovered: boolean = false;

  /**
   * Emitted when the radio button selection changes
   */
  @Event() bhChange!: EventEmitter<string>;

  componentDidLoad() {
    syncCheckableFormValue(this.internals, this.name, this.selected, this.value);
  }

  formResetCallback() {
    this.selected = false;
    syncCheckableFormValue(this.internals, this.name, this.selected, this.value);
  }

  private handleClick = (): void => {
    if (this.disabled || this.selected) return;

    this.selected = true;
    syncCheckableFormValue(this.internals, this.name, this.selected, this.value);
    this.bhChange.emit(this.value);
  };

  private handleKeyDown = (event: KeyboardEvent): void => {
    if (event.key === ' ' || event.key === 'Enter') {
      event.preventDefault();
      this.handleClick();
    }
  };

  private handleFocus = (): void => {
    this.isFocused = true;
  };

  private handleBlur = (): void => {
    this.isFocused = false;
  };

  private handleMouseEnter = (): void => {
    this.isHovered = true;
  };

  private handleMouseLeave = (): void => {
    this.isHovered = false;
  };

  private uniqueId = `bh-radio-${Math.random().toString(36).substring(2, 9)}`;

  private renderRadioControl() {
    const radioClasses = {
      'radio': true,
      [`radio-${this.size}`]: true,
      'radio-selected': this.selected,
      'radio-disabled': this.disabled,
      'radio-focused': this.isFocused && !this.disabled,
      'radio-hover': this.isHovered && !this.disabled,
    };

    return (
      <div
        class="radio-control"
        onClick={this.handleClick}
        onKeyDown={this.handleKeyDown}
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
        onFocus={this.handleFocus}
        onBlur={this.handleBlur}
        tabIndex={this.disabled ? -1 : 0}
        role="radio"
        aria-checked={String(this.selected)}
        aria-disabled={String(this.disabled)}
      >
        <div class={radioClasses} part="radio">
          {this.selected && <div class="radio-dot"></div>}
        </div>
      </div>
    );
  }

  render() {
    const hasLabel = this.label || this.supportingText;

    const wrapperClasses = {
      'radio-wrapper': true,
      'radio-wrapper-with-label': !!hasLabel,
      'radio-wrapper-disabled': this.disabled,
    };

    // Render with bh-label when label/supportingText is provided
    if (hasLabel) {
      return (
        <div class={wrapperClasses} part="wrapper">
          <bh-label
            layout="inline-start"
            label={this.label}
            supportingText={this.supportingText}
            disabled={this.disabled}
            for={this.uniqueId}
          >
            {this.renderRadioControl()}
          </bh-label>
        </div>
      );
    }

    // Standalone radio button without label
    return (
      <div class={wrapperClasses} part="wrapper">
        {this.renderRadioControl()}
      </div>
    );
  }
}
